<template>
  <div id='chart2' >
    <apexchart type=bar :width="width" height=350 :options="chartOptions" :series="series" />
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'ChartWeek',
  props: ['dataChartWeek', 'width'],
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: [],
      chartOptions: {
       
      }
    }
  },
  watch: {
    dataChartWeek() {
      this.getData()
      this.getCategories()
    }
  },
  methods: {
    getData() {
      this.series = [{
        name: 'Total del dia',
        data: this.dataChartWeek.map(a => a.total)
      }]
    },
    getCategories() {
      
    //cat = this.dataChartWeek.map(a => a.date)
      //cat = ['Lunes','Martes','Miercoles','Jueves','Viernes','Sabado','Domingo']
      this.chartOptions = {
         chart: {
          height: 350,
          type: 'bar',
        },
        plotOptions: {
          bar: {
            dataLabels: {
              position: 'top', // top, center, bottom
            },
          }
        },
        colors: ['#62cb31'],
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return '$' + val
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ['#304758']
          }
        },
        xaxis: {
          categories: this.dataChartWeek.map(a => a.date),
          position: 'top',
          labels: {
            offsetY: -18
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          },
          crosshairs: {
            fill: {
              type: 'gradient',
              gradient: {
                colorFrom: '#62cb31',
                colorTo: '#62cb31',
                stops: [0, 100],
                opacityFrom: 0.4,
                opacityTo: 0.5,
              }
            }
          },
          tooltip: {
            enabled: true,
            offsetY: -35,

          }
        },
        fill: {
          gradient: {
            shade: 'light',
            type: 'horizontal',
            shadeIntensity: 0.25,
            gradientToColors: undefined,
            inverseColors: true,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [50, 0, 100, 100]
          },
        },
        yaxis: {
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false,
          },
          labels: {
            show: false,
            formatter: function (val) {
              return '$' + val
            }
          }
        }
      }
      
      //console.log(this.dataChartWeek.map(a => a.date))
    }
  }
}
</script>

<style scoped>
  #chart2{
    width: 100%;
  }
</style>
